.header {
	height: 70px;
	background: #161616;
	width: 100%;
	margin-bottom: 60px;

	.inner {
		height: 70px;
		display: flex;
		justify-content: space-between;
		align-items: center;

		.logo {
			padding: 0px 10px;
			background: #222222;
			border-radius: 6px;
			height: 35px;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		.baseCurrencyWrap {
			display: flex;
			align-items: center;
			background: #161616;
			border-radius: 6px;

			& span {
				margin-right: 10px;
			}
		}
	}
	@media (max-width: 464px) {
		.logo {
			padding: 0px 4px;
			font-size: 10px;
		}
	}
	@media (max-width: 390px) {
		.baseCurrencyWrap {
			& span {
				display: none;
			}
		}
	}
}
